  font-family: 'Play';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/play/v19/6aez4K2oVqwIjtI.ttf) format('truetype');
}
@font-face {
  font-family: 'Play';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/play/v19/6ae84K2oVqwItm4TOpc.ttf) format('truetype');
}
tr,
p,
input,
button,
h3,
h1,
h2,
label {
  font-family: "Play", sans-serif !important;
  font-weight: 400 !important;
  font-style: normal !important;
}
.basicfont {
  font-family: "Play", sans-serif !important;
  font-weight: 400 !important;
  font-style: normal !important;
}
h1 {
  font-weight: 600 !important;
  text-align: center;
}
.btn:hover {
  color: #000000 !important;
}
.input-hidden {
  display: none;
}
.ub-money-cont {
  position: absolute;
  background-color: rgba(13, 202, 240, 0.7);
  text-align: center;
  border-radius: 20px 20px 20px 20px;
}
.ub-money {
  display: inline-block;
  color: #ffffff;
}
.ub-money-cont-desk {
  position: absolute;
  background-color: rgba(13, 202, 240, 0.7);
  text-align: center;
  border-radius: 20px 20px 20px 20px;
  width: 14rem;
  height: fit-content;
  top: 1%;
  right: 1%;
}
.ub-money-img-desk {
  position: relative;
  width: 4.4rem;
  left: -10%;
}
.ub-money-desk {
  display: inline-block;
  color: #ffffff;
  font-size: 2rem;
}
.ub-money-cont-mob {
  position: absolute;
  background-color: rgba(13, 202, 240, 0.7);
  text-align: center;
  border-radius: 20px 20px 20px 20px;
  width: 7.5rem;
  height: fit-content;
  top: 1%;
  right: 1%;
}
.ub-money-img-mob {
  position: relative;
  width: 3rem;
  left: -10%;
}
.ub-money-mob {
  display: inline-block;
  color: #ffffff;
}
