  border-radius: 50%;
  margin-top: 1%;
  margin-left: 2%;
  background-color: #0dcaf0;
  width: 5.5rem;
}
.ub-btn-mob {
  border-radius: 50%;
  margin-top: 1%;
  margin-left: 2%;
  background-color: #0dcaf0;
  width: 4rem;
}
